import React from "react";
import { useForm } from 'react-hook-form';
import { AppContext } from "../../context-API/ContextContainer";
import { useContext, useState } from "react";
import { Link, useHistory } from "react-router-dom";

const SignIn = () => {
  const { register, handleSubmit, errors } = useForm();
  const { setUserInfo } = useContext(AppContext);
  const history = useHistory();
  const [error, setError] = useState("")


  const onSubmit = async (data) => {
    console.log({ data });
    try {
      const res = await (
        await fetch("http://localhost:4000/auth/login", {
          method: "POST",
          body: JSON.stringify(data),
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
        })
      ).json();
      console.log(res);
      if (res.user) {
        localStorage.setItem(
          "user" , (JSON.stringify (res.user))
        )
        setUserInfo(res.user);
        history.push("/user-dashboard");
      } else {
        setError(res.message)
      }


      return res;
    } catch (error) {
      // catch route is NOT called on error responses from the backend
      // like 404 and 500
      console.log(error);

      return [];
    }
  };

  return (
    <div className="sign-in form-spacer">
      <div className="site-content container">
        <div class="section top-sub-label">
          <p>
            <a href="/" target="_self">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="left"
                width="30"
                height="30"
              >
                <g
                  transform="rotate(-90 15 15)"
                  fill="rgba(84,100,248,0)"
                  stroke="#283042"
                  stroke-width="2"
                >
                  <rect width="30" height="30" rx="15" stroke="none" />
                  <rect
                    x="1"
                    y="1"
                    width="28"
                    height="28"
                    rx="14"
                    fill="none"
                  />
                </g>
                <g
                  fill="none"
                  stroke="#f6f6f7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-miterlimit="10"
                  stroke-width="1.5"
                >
                  <path d="M9.68 15.1h11.05M13.45 18.9l-3.93-3.8 3.93-3.8" />
                </g>
              </svg>
            </a>
            Sign in
          </p>
        </div>

        <div className="section form-reg-log">
          <h1 className="h1 txt-center">Sign In to EagleLink</h1>

          <div className="form-container">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="form-group">
                <input
                  name="email"
                  className="form-desdign"
                  idName="user_email"
                  type="email"
                  placeholder="email@example.com"
                  name="email"
                  ref={register({
                    required: 'Please put your email sir.',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message:
                        'Email is invalid. Please fix',
                    },
                  })}
                />
                <div className="error-message">
                  {errors.email && <span>{errors.email.message}</span>}
                </div>
                <input
                  className="form-desdign"
                  idName="user_password"
                  type="password"
                  placeholder="password"
                  autocomplete="off"
                  name="password"
                  ref={register({
                    required: 'Required',
                    minLength: {
                      value: 5,
                      message:
                        'Password must be at least 5 characters',
                    },
                  })}
                />
                <div className="error-message">
                  {errors.password && (
                    <span>{errors.password.message}</span>
                  )}
                </div>
              </div>
              <div className="submit">
                <button className="btn full-btn" type="submit">
                  Sign in
                </button>
              </div>
            </form>
            <div className="error-login">
              {error}
            </div>
          </div>


          <p className="sub-info">
            <Link to="/auth/signup">
              Don't have an account?
            </Link>{" "}
            •
            <Link href="/forgot-password" target="_self">
              Forgot Password?
            </Link>{" "}
            •
            <Link href="/privacy-policy" target="_self">
              Privacy Policy
            </Link>
          </p>
        </div>
      </div>
    </div>
  );
};
export default SignIn;
