import React, { useEffect } from "react";
import LinkUser from "./LinkUser";
import { Link, useHistory } from "react-router-dom";

import { AppContext } from "../../context-API/ContextContainer";
import { useContext, useState } from "react";

export default function UserDashboard(props) {
  const { userInfo, setUserInfo } = useContext(AppContext);
  const { firstName, lastName, avatar } = userInfo;
  const history = useHistory();

  const [showLinks, setShowLinks] = useState(true);


  const handleLogout = async () => {
    // first step make a post call
    // second redirect to front page /
    try {
      const res = await (
        await fetch("http://localhost:4000/auth/logout", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
        })
      ).json();
      setUserInfo({ user: {} });
      history.push("/");

      return res;
    } catch (error) {
      // catch route is NOT called on error responses from the backend
      // like 404 and 500
      console.log(error);

      return [];
    }
  };

  return (
    <div className="user-dashboard mb-96">

      <div className="site-content container form-spacer">
        <div className="site-content container">
          <div class="section top-sub-label">
            <p>
              Profile
            <Link to="/user-dashboard" >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  className="left"
                  width="30"
                  height="30"
                >
                  <g
                    transform="rotate(-90 15 15)"
                    fill="rgba(84,100,248,0)"
                    stroke="#283042"
                    stroke-width="2"
                  >
                    <rect width="30" height="30" rx="15" stroke="none" />
                    <rect
                      x="1"
                      y="1"
                      width="28"
                      height="28"
                      rx="14"
                      fill="none"
                    />
                  </g>
                  <g
                    fill="none"
                    stroke="#f6f6f7"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                    stroke-miterlimit="10"
                    stroke-width="1.5"
                  >
                    <path d="M9.68 15.1h11.05M13.45 18.9l-3.93-3.8 3.93-3.8" />
                  </g>
                </svg>
              </Link>
            </p>
          </div>
        </div>
        <div className="grid grid--gutters grid--full">
          <div className="grid-cell">
            <h1 className="h2 txt-center">
              My Profile Settings
          </h1>
            <h2 className="h2">
              {firstName} {lastName}
            </h2>
          </div>
        </div>

        <div className="grid grid--gutters grid--full">
          <div className="grid-cell">
            <Link to="/account" className="txt-read">
              <p>
                Account <span className="f-right">▶</span>
              </p>
            </Link>
          </div>
          <div className="grid-cell">
            <Link to="" className="txt-read">
              <p>Prefrences Design  <span className="f-right">▶</span></p>
            </Link>
          </div>
          <div className="grid-cell">
            <Link to="" className="txt-read">
              <p>Connections  <span className="f-right">▶</span></p>
            </Link>
          </div>
          <div className="grid-cell">
            <Link to="" className="txt-read">
              <p>Privacy  <span className="f-right">▶</span></p>
            </Link>
          </div>
          <div className="grid-cell">
            <a href="/" target="_self" onClick="handleLogout">
              <button className="btn full-btn secondary">
                Logout
            </button>
            </a>

          </div>

        </div>
      </div>
    </div>
  );
}
