import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { AppContext } from "../../context-API/ContextContainer";
import { useContext } from "react";

import img_1 from "../assets/img/welcome/img-1.jpg";
import media from "../assets/demo/img-place-holder.jpg";
import zoom from "../assets/img/ui-svg/zoom.svg";

export default function PostAdd(props) {
  const { userInfo } = useContext(AppContext);
  const { firstName, lastName, _id, displayName, creationDate, url, text } = userInfo;
  const history = useHistory();
  const [LinkLabel, setLinkLabel, image, setImage] = useState("");
  const [LinkUrl, setLinkUrl] = useState("");
  const [LinkBtn, setLinkBtn] = useState("");

  const [loading, setLoading] = useState(false);

  const uploadImage = async e => {
    const files = e.target.files;
    const data = new FormData()
    data.append("image", files[0]);
    data.append("upload_preset", "darwin")
    setLoading(true);
    const res = await fetch(
      "http://localhost:4000/posts/upload",
      {
        method: "Post",
        body: data
      }
    )
    const file = await res.json();
    setImage(file.secure_url);
    setLoading(false);
  }

  const onSubmit = async () => {
    console.log({ LinkLabel, LinkUrl, _id });
    const data = { url: LinkUrl, text: LinkLabel, userID: _id };

    try {
      const res = await (
        await fetch("http://localhost:4000/posts", {
          method: "POST",
          body: JSON.stringify(data),
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
        })
      ).json();
      console.log(res);
      history.push("/user-dashboard");
      return res;
    } catch (error) {
      // catch route is NOT called on error responses from the backend
      // like 404 and 500
      console.log(error);

      return [];
    }
  };

  return (
    <div className="site-content container form-spacer">
      <div className="site-content container">
        <div class="section top-sub-label">
          <p>
            Write your Post
            <a href="/chose-add" target="_self">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="left"
                width="30"
                height="30"
              >
                <g
                  transform="rotate(-90 15 15)"
                  fill="rgba(84,100,248,0)"
                  stroke="#283042"
                  stroke-width="2"
                >
                  <rect width="30" height="30" rx="15" stroke="none" />
                  <rect
                    x="1"
                    y="1"
                    width="28"
                    height="28"
                    rx="14"
                    fill="none"
                  />
                </g>
                <g
                  fill="none"
                  stroke="#f6f6f7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-miterlimit="10"
                  stroke-width="1.5"
                >
                  <path d="M9.68 15.1h11.05M13.45 18.9l-3.93-3.8 3.93-3.8" />
                </g>
              </svg>
            </a>
          </p>
        </div>
      </div>

      <div className="grid grid--gutters" id="[-userID-]">
        <div className="post grid-cell">
          <div className="post-container tile">
            <div className="post-header">
              <div className="grid--center">
                <div className="grid-cell">
                  <div className="grid grid--center">
                    <div className="user-avatar">
                      <Link to="/my-profile" target="_self">
                        <img
                          className="photo"
                          src={img_1}
                          decoding="async"
                          loading="lazy"
                        />
                      </Link>
                    </div>
                    <div className="gird">
                      <span className="h3">{firstName} {lastName}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="grid date-published">
              <span className="txt-sub">[-creationDate-]</span>
            </div>
            <div className="grid post-media-section">
              <div className=" post-media-container" >
                <div className="grid post-media-medium">
                  <img
                    className="media-place-holder"
                    src={
                      loading ? (
                        <span className="h3">
                          Loading ...
                        </span>
                      ) : (
                          <img src={image} />
                        )
                    }
                    decoding="async"
                    loading="lazy"
                  />
                </div>
                {/*
                <div className="media-action img-zoom">
                  <a href="#" target="_blank">
                    <img
                      className="gird"
                      src={zoom}
                      decoding="async"
                      loading="lazy"
                    />
                        Show full image
                        </a>
                </div>-->
                */}
              </div>
            </div>
            <div className="grid post-description">
              <div class="grid-cell">
                <p>{LinkLabel}</p>
              </div>
            </div>
            <div className="grid post-btn">
              <div class="grid-cell">
                <Link to="">
                </Link>
                <button className="btn btn-primary full-btn">{LinkBtn}</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <form >
        <div className="grid grid--gutters grid--full">
          <div className="grid-cell">
            <p>
              <span className="txt-sub">Discription</span>
            </p>
            <textarea
              placeholder="Share your feedback and comments!"
              required="true"
              name="text"
              spellcheck="false"
              value={LinkLabel}
              onChange={(e) => setLinkLabel(e.target.value)}
            ></textarea>
            <br />
          </div>
          <div className="grid-cell pb-12">
            <p>
              <span className="txt-sub">Upload Image</span>
            </p>
            <input id="file-input"
              type="file"
              name="file"
              placeholder="Choose a Photo"
              onChange={uploadImage}
            />
            <label for="file-input">Chose a Photo</label>
            <br />
          </div>
          <div className="grid-cell pb-12">
            <p>
              <span className="txt-sub">Button Link</span>
            </p>
            <input
              name="url"
              placeholder="https//www.eagle-link.com"
              value={LinkUrl}
              onChange={(e) => setLinkUrl(e.target.value)}
            />
            <br />
          </div>
          <div className="grid-cell">
            <p>
              <span className="txt-sub">Button Text</span>
            </p>
            <input
              placeholder="Button Text"
              name="text-button"
              spellcheck="false"
              value={LinkBtn}
              onChange={(e) => setLinkBtn(e.target.value)}
            ></input>
            <br />
          </div>


          <div className="grid-cell">
            <div className="grid grid--gutters">
              <div className="grid-cell">
                <a href="/user-dashboard" target="_self" onClick="handleLogout">
                  <button className="btn secondary right" target="_self">
                    Cancel
                  </button>
                </a>
              </div>
              <div className="grid-cell">
                <button
                  className="btn primary right f-right submit"
                  target="_self"
                >
                  Save
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    </div>
  );
}
