import React, { useState } from "react";
import { Link, useHistory } from "react-router-dom";
import { AppContext } from "../../context-API/ContextContainer";
import { useContext } from "react";

import img_1 from "../assets/img/welcome/img-1.jpg";


export default function VideoAdd(props) {
  const { userInfo } = useContext(AppContext);
  const { firstName, lastName , _id, displayName, creationDate, url, text  } = userInfo;
  const history = useHistory();
  const [ LinkLabel, setLinkLabel ] = useState("");
  const [ LinkUrl, setLinkUrl ] = useState("");
  
  const onSubmit = async () => {
    console.log({ LinkLabel, LinkUrl, _id });
    const data = { url : LinkUrl, text : LinkLabel, userID : _id };

    try {
      const res = await (
        await fetch("http://localhost:4000/posts", {
          method: "POST",
          body: JSON.stringify(data),
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include",
        })
      ).json();
      console.log(res);
      
      history.push("/user-dashboard");
      return res;
    } catch (error) {
      // catch route is NOT called on error responses from the backend
      // like 404 and 500
      console.log(error);

      return [];
    }
  };

  return (
    <div className="site-content container form-spacer">
      <div className="site-content container">
        <div class="section top-sub-label">
          <p>
            Share your video
            <a href="/chose-add" target="_self">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="left"
                width="30"
                height="30"
              >
                <g
                  transform="rotate(-90 15 15)"
                  fill="rgba(84,100,248,0)"
                  stroke="#283042"
                  stroke-width="2"
                >
                  <rect width="30" height="30" rx="15" stroke="none" />
                  <rect
                    x="1"
                    y="1"
                    width="28"
                    height="28"
                    rx="14"
                    fill="none"
                  />
                </g>
                <g
                  fill="none"
                  stroke="#f6f6f7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-miterlimit="10"
                  stroke-width="1.5"
                >
                  <path d="M9.68 15.1h11.05M13.45 18.9l-3.93-3.8 3.93-3.8" />
                </g>
              </svg>
            </a>
          </p>
        </div>
      </div>

      <div className="grid grid--gutters" id="[-userID-]">
        <div className="post grid-cell">
          <div className="post-container tile">
            <div className="post-header">
              <div className="grid--center">
                <div className="grid-cell">
                  <div className="grid grid--center">
                    <div className="user-avatar">
                      <a href="/my-profile" target="_self">
                        <img
                          className="photo"
                          src={img_1}
                          decoding="async"
                          loading="lazy"
                        />
                      </a>
                    </div>
                    <div className="gird">
                      <span className="h3">{firstName} {lastName}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="grid date-published">
              <span className="txt-sub">{creationDate}</span>
            </div>
            <div className="grid post-video-iframe">
              <div class="grid-cell">
                <lite-youtube>
                <iframe 
                width="560" 
                height="315" 
                frameborder="0" 
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                allowfullscreen="" 
                src="https://www.youtube.com/embed/RUs1YrXzvJU?autoplay=0">
                </iframe>
                </lite-youtube>
              </div>
            </div>
            <div className="grid post-description">
              <div class="grid-cell">
                <p>{ LinkLabel }</p>
              </div>
            </div>
          </div>
        </div>
      </div>
      <form /*onSubmit={handleSubmit(onSubmit)}*/>
        <div className="grid grid--gutters grid--full">
          <div className="grid-cell pb-12">
            <p>
              <span className="txt-sub">
                Video Link
              </span>
            </p>
            <input
              name="url"
              placeholder="https://youtu.be/TNhaISOUy6Q"
              value={LinkUrl}
              onChange={ (e) => setLinkUrl(e.target.value) }
            />
            <br />
          </div>
          <div className="grid-cell">
            <p>
              <span className="txt-sub">Discription</span>
            </p>
            <textarea
              placeholder="Share your feedback and comments!"
              required="true"
              name="text"
              spellcheck="false"
              value={LinkLabel}
              onChange={ (e) => setLinkLabel(e.target.value) }
            ></textarea>
            <br />
          </div>
          <div className="grid-cell">
            <div className="grid grid--gutters">
              <div className="grid-cell">
                <a href="/user-dashboard" target="_self" onClick="handleLogout">
                  <button className="btn secondary right" target="_self">
                    Cancel
                  </button>
                </a>
              </div>
              <div className="grid-cell">
                <button
                  className="btn primary right f-right submit"
                  target="_self"
                >
                  Save
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
      
    </div>
  );
}
