import React from 'react';
import { useForm } from 'react-hook-form';
import { AppContext } from '../../context-API/ContextContainer';
import { useContext } from 'react';
import { Link, useHistory } from 'react-router-dom';


const SignUp = () => {
  const { register, handleSubmit, errors } = useForm();
  const { setUserInfo } = useContext(AppContext);
  const history = useHistory();

  const onSubmit = async (data) => {
    console.log({ data });
    try {
      const res = await (
        await fetch('http://localhost:4000/auth/signup', {
          method: 'POST',
          body: JSON.stringify(data),
          headers: {
            'Content-Type': 'application/json',
          },
          credentials: 'include',
        })
      ).json();
      console.log(res);
      setUserInfo(res);
      history.push('/user-dashboard');

      return res;
    } catch (error) {
      // catch route is NOT called on error responses from the backend
      // like 404 and 500
      console.log(error);

      return [];
    }
  };
  return (
    <div className="sign-in form-spacer">
      <div className="site-content container">
        <div class="section top-sub-label">
          <p>
            <a href="/" target="_self">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="left"
                width="30"
                height="30"
              >
                <g
                  transform="rotate(-90 15 15)"
                  fill="rgba(84,100,248,0)"
                  stroke="#283042"
                  stroke-width="2"
                >
                  <rect
                    width="30"
                    height="30"
                    rx="15"
                    stroke="none"
                  />
                  <rect
                    x="1"
                    y="1"
                    width="28"
                    height="28"
                    rx="14"
                    fill="none"
                  />
                </g>
                <g
                  fill="none"
                  stroke="#f6f6f7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                  stroke-miterlimit="10"
                  stroke-width="1.5"
                >
                  <path d="M9.68 15.1h11.05M13.45 18.9l-3.93-3.8 3.93-3.8" />
                </g>
              </svg>
            </a>
            Sign up
          </p>
        </div>

        <div className="section form-reg-log">
          <h1 className="h1 txt-center">
            Creat your account
          </h1>
          <div className="form-container">
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="info">
                <input
                  name="firstName"
                  defaultValue="Noel"
                  ref={register({
                    required: 'Please put a firstname, sir',
                  })}
                />
                <div className="error-message">
                  {errors.firstName && (
                    <span>{errors.firstName.message}</span>
                  )}
                </div>

                <input
                  name="lastName"
                  defaultValue="Fielding"
                  ref={register({
                    required:
                      'Please put your lastname sir.',
                  })}
                />
                <div className="error-message">
                  {errors.lastName && (
                    <span>{errors.lastName.message}</span>
                  )}
                </div>

                <input
                  name="userName"
                  defaultValue="Richmond"
                  ref={register({
                    required:
                      'Please put your username sir.',
                  })}
                />
                <div className="error-message">
                  {errors.userName && (
                    <span>{errors.userName.message}</span>
                  )}
                </div>

                <input
                  name="email"
                  defaultValue="test@gmail.com"
                  ref={register({
                    required: 'Please put your email sir.',
                    pattern: {
                      value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                      message:
                        'Email is invalid. Please fix',
                    },
                  })}
                />
                <div className="error-message">
                  {errors.email && (
                    <span>{errors.email.message}</span>
                  )}
                </div>

                <input
                  name="password"
                  defaultValue="0123456789"
                  type="password"
                  ref={register({
                    required: 'Required',
                    minLength: {
                      value: 10,
                      message:
                        'Password must be at least 10 characters',
                    },
                  })}
                />
                <div className="error-message">
                  {errors.password && (
                    <span>{errors.password.message}</span>
                  )}
                </div>
              </div>
              <div className="submit">
                <input type="submit" />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};
export default SignUp;
