import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';

// imported components +++
import WelcomePage from './components/pages/WelcomePage';
import SignUp from './components/pages/SignUp';
import SignIn from './components/pages/SignIn';
import ForgotPassword from './components/pages/ForgotPassword';
import PrivacyPolicy from './components/pages/PrivacyPolicy';
import UserDashboard from './components/pages/UserDashboard';
import UserFeed from './components/pages/UserFeed';
import ChoseAdd from './components/pages/ChoseAdd';
import TxtLinkAdd from './components/pages/TxtLinkAdd';
import VideoAdd from './components/pages/VideoAdd';
import PostAdd from './components/pages/PostAdd';
import UserNotification from './components/pages/UserNotification';
import UserAccSettings from './components/pages/UserAccSettings';
import LandingSettings from './components/pages/LandingSettings';
import MyProfile from './components/pages/MyProfile';

// +++ Context +++
import { ContextContainer } from '../src/context-API/ContextContainer';

function App(props) {
  return (
    <>
      <ContextContainer>
        <Router>
          <Switch>
            <Route exact path="/" component={WelcomePage} />
            <Route
              exact
              path="/auth/signup"
              component={SignUp}
            />
            <Route
              exact
              path="/auth/signin"
              component={SignIn}
            />
            <Route
              exact
              path="/forgot-password"
              component={ForgotPassword}
            />
            <Route
              exact
              path="/privacy-policy"
              component={PrivacyPolicy}
            />
            <Route
              exact
              path="/user-dashboard"
              component={UserDashboard}
            />
            <Route
              exact
              path="/user-feed"
              component={UserFeed}
            />
            <Route
              exact
              path="/chose-add"
              component={ChoseAdd}
            />
            <Route
              exact
              path="/txtlink-add"
              component={TxtLinkAdd}
            />
            <Route
              exact
              path="/video-add"
              component={VideoAdd}
            />
            <Route
              exact
              path="/post-add"
              component={PostAdd}
            />
            <Route
              exact
              path="/notification"
              component={UserNotification}
            />
            <Route
              exact
              path="/account"
              component={UserAccSettings}
            />
            <Route
              exact
              path="/landing-settings"
              component={LandingSettings}
            />
            <Route
              exact
              path="/my-profile"
              component={MyProfile}
            />
          </Switch>
        </Router>
      </ContextContainer>
    </>
  );
}

export default App;